import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App, AlertController } from 'ionic-angular';
import { TipoUsuario, Usuario} from '../../models/usuario';
import { LoginPage } from '../../pages/login/login';
import { FirebaseDbProvider} from '../../providers/firebase-db/firebase-db';
import { Entrega} from '../../models/entrega';
import { ToastController } from 'ionic-angular';
import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  GoogleMapOptions,
  CameraPosition,
  MarkerOptions,
  Marker
} from '@ionic-native/google-maps';

/**
 * Generated class for the RepartidorDetalladoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-repartidor-detallado',
  templateUrl: 'repartidor-detallado.html',
})
export class RepartidorDetalladoPage {
	listaUsuarios: any;
	id='';

  constructor(public navCtrl: NavController, public navParams: NavParams, public app: App, 
				public dbFirebase:FirebaseDbProvider, public toastCtrl: ToastController,
				public geolocation: Geolocation) {
	this.id=navParams.get('id');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RepartidorDetalladoPage');
	//this.loadMap();
  }
  
  loadMap(){
	  let mapOptions: GoogleMapOptions = {
		camera: {
		  target: {
			lat: 43.0741904, // default location
			lng: -89.3809802 // default location
		  },
		  zoom: 18,
		  tilt: 30
		}
	  };

	this.map = this.googleMaps.create('map_canvas', mapOptions);

	  // Wait the MAP_READY before using any methods.
	  this.map.one(GoogleMapsEvent.MAP_READY)
	  .then(() => {
		// Now you can use all methods safely.
		this.getPosition();
	  })
	  .catch(error =>{
		console.log(error);
	  });
}

	getPosition(): void{
	  this.map.getMyLocation()
	  .then(response => {
		this.map.moveCamera({
		  target: response.latLng
		});
		this.map.addMarker({
		  title: 'My Position',
		  icon: 'blue',
		  animation: 'DROP',
		  position: response.latLng
		});
	  })
	  .catch(error =>{
		console.log(error);
	  });
	}
	
  ionViewDidEnter(){
		this.dbFirebase.getUsuarios().subscribe(listaUsuarios=>{this.listaUsuarios=listaUsuarios;});
  }

  goToLoginPage() {
	const root = this.app.getRootNav();
	root.popToRoot();
  }
  
}



